// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
'use strict';

import PixelRouteComponent from 'components/routes/PixelRouteComponent.vue'
import BarRouteComponent from 'components/routes/BarRouteComponent.vue'

import Vue from 'vue'
// import Bourgeon from 'bourgeon'

import App from './App'
// import routes from 'routes';

// Vue.use(Bourgeon, {
//   locales: ['fr', 'en']
// });

import VueRouter from 'vue-router'

Vue.use(VueRouter);
const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: PixelRouteComponent
    },
    {
      name: 'details',
      path: '/details',
      component: BarRouteComponent
    }
  ]
  // short for routes: routes
})


/* eslint-disable no-new */
const app = new Vue({
  router,
  render: h => h(App)
}).$mount('#app');

export default router;



