var path = require('path');

export default {
  root_path: '',
  currentRoute: '/',
  currentItems: [],
  data: {
    countries: {
      title: 'OÙ AIMERAIS-TU TRAVAILLER ?',
      url: path.resolve(__dirname, '../static/data/countries-cinema.csv'),
      frequencies: {
        title: 'VAS - TU SOUVENT AU CINÉ ?',
        legend_graph: {
          axe_x: ['jamais', '-1', '1', '2', '3', 'plus de 3' ]
        }
      },
      legend: 'Étude menée sur 37 étudiants DDI de l’année 2016 de façon tout à fait anonyme (mais on peut deviner certains trucs)',
      row: function (d) {
        let frequencies = [];
            frequencies.push(d.never, d.less_one_by_month, d.one_by_month, d.two_by_month, d.three_by_month, d.more_three_by_month)
        return {
          title: d.country,
          frequencies: frequencies,
          color: d.color,
          num: d.num
        }
      }
    }
  }
}



