<template>
  <aside class="sidebar-container graph-legend-container">
    <h2 v-if="this.$route.name != 'details'" class="legend-title">{{ data.title }}</h2>
    <h2 v-else class="legend-title">{{ data.frequencies.title }}</h2>
      <transition-group name="slide-top" tag="ul" class="graph-legend-items">
        <PixelSidebarItem v-for="d in data.parsed" v-bind:key="d" :item="d" :min="min" :max="max"></PixelSidebarItem>
      </transition-group>
  </aside>
</template>

<script type="text/babel">
  import PixelSidebarItem from './PixelSidebarItem.vue'

  export default {
    props: {
      data: Object,
    },
    data() {
      return {
        min: this.data.min,
        max: this.data.max
      }
    },
    components: {
      PixelSidebarItem
    }
  }
</script>

<style type="text/css">
.graph-legend-items {
  width: 100%;
  display: flex;
  margin-top: 20px;
  max-height: 285px;
  flex-direction: column;
  flex-wrap: wrap;
}

.legend-title {
  font-family: futura-pt, sans-serif;
  font-weight: 700;
  font-size: 1.1em;
  line-height: 1.1em;
  text-transform: uppercase;
}

.legend-title::after {
  content: '';
  display: block;
  width: 40px;
  height: 1px;
  padding-top: 15px;
  border-bottom: 1px solid #000;
}


.slide-top-enter-active {
  transition: margin .3s ease, opacity 250ms ease-out;
}

.slide-top-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.slide-top-enter, .slide-top-leave-active {
  margin-top: -10px;
  opacity: 0;
}

</style>



