<template>
  <div class="content-container">
      <GraphComponent v-if="data" :data="data"></GraphComponent>
      <SidebarComponent v-if="data" :data="data"></SidebarComponent>
  </div>
</template>

<script>
  import GraphComponent from '../graphs/PixelGraph.vue'
  import SidebarComponent from '../sidebar/PixelSidebar.vue'
  import * as d3 from "d3";
  import store from 'store';

  export default {
    data() {
      return {
        data: null,
        name: 'countries'
      }
    },
    components: {
      GraphComponent,
      SidebarComponent
    },
    methods: {
      getData() {
        d3.csv(store.root_path + store.data[this.name].url).row(store.data[this.name].row).get((d)=> {
          store.data[this.name].parsed = d;
          store.data[this.name].min = 0;
          let max = 0;
          let maxFrequency = 0;
          for(let i of d) {
            max+= +i.num;
            for (let j of i.frequencies) {
            maxFrequency = j > maxFrequency ? j : maxFrequency;
            }
          }
          store.data[this.name].max = max;
          store.data[this.name].frequencies.max = maxFrequency;
          this.data = store.data[this.name]
        });
      },
    },
    created() {
      this.getData();
    }
  }
</script>

<style type="text/css">
.content-container {
  display: flex;
}
</style>



