<template>
  <!--<transition name="slide-top">-->
    <li @mouseover="hover"  class="graph-legend-item" :class="opacityItem":id="item.title+'_link-item'">
      <span class="item-legend-color" :style="itemColor"></span>
      <a @click.capture="updateCurrentItems">{{ item.title }}</a>
      <!--<router-link @click.capture="updateCurrentItems" :to="{name:'details'}">{{ item.title }}</router-link>-->
      <span v-if="this.$route.name != 'details'" class="item-percent">{{percentValue}}</span>
      <span v-else class="item-percent">{{item.num}}</span>
    </li>
  <!--</transition>-->
</template>

<script type="text/babel">
  import * as d3 from "d3";
  import store from 'store';
  export default {
    props: {
      min: 0,
      max: 0,
      item: Object
    },
    methods: {
      hover() {
        this.$emit('hover-country')
      },
      updateCurrentItems() {
        if (store.currentItems.find((d) => {
            return d === this.item.title
          })) {
          store.currentItems.splice(store.currentItems.indexOf(this.item.title), 1)
        } else {
          store.currentItems.push(this.item.title);
        }
        this.$router.push({
          name: 'details'
        })
      }
    },
    computed: {
      itemColor() {
        return {
          backgroundColor: this.item.color
        }
      },
      percentValue() {
        let self = this;
        let scale = d3.scaleLinear()
          .domain([self.min, self.max])
          .range([0, 1 ]);
        return d3.format(".0%")(scale(self.item.num))
      },
      opacityItem() {
        if(this.$route.name == 'details') {
          if (store.currentItems.find((d) => {
              return d === this.item.title
            })) {
            return ''
          } else {
            return 'opacity'
          }
        }
        return '';
      }
    }
  }

</script>

<style type="text/css">
  .graph-legend-item {
    display: inline-block;
    width: 50%;
    font-family: futura-pt, sans-serif;
    font-weight: 400;
    font-size: .8em;
    line-height: .8em;
    letter-spacing: .01em;
    margin-bottom: 10px;
    cursor: pointer;
  }

  .graph-legend-item.opacity {
    opacity: 0.2;
    /*transition: opacity 250ms ease;*/
  }
  .graph-legend-item.opacity:hover {
    opacity: 0.75;
  }

  .graph-legend-item a {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }

  .item-legend-color {
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-right: 10px;
    transform-origin: 50% 50%;
    transition: transform 200ms ease;
  }

  .item-percent {
    font-family: futura-pt, sans-serif;
    font-weight: 600;
    font-size: 1.2em;
    margin-left: 7px;
    display: none;
  }

  .graph-legend-item:hover .item-percent{
    display: inline-block;
  }

  .graph-legend-item:hover .item-legend-color{
    transform: rotate(45deg);
  }

</style>



