<template>
  <main class='main-container graph-container'>
    <div :id='id'></div>
  </main>
</template>

<script type='text/babel'>
  import * as d3 from 'd3';
  import store from 'store';

  export default {
  props: {
    data: Object
  },
  data() {
    return {
      width: 650,
      height: 460,
      svg: null,
      id: 'barGraph-container',
      ratio: 0.2,
      marginBottom: 30,
      marginTop:30,
      marginLeft:30,
      currentData: this.$route.params.name,
      ratioLosange: 0.4,
      bar: '',
      dataItem: null,
      x: null,
      y: null,
      state:store
    }
  },
  computed: {
    paddingLeft() {
      return Math.round(this.width * this.ratio)
    },
    barRange() {
      return Math.round(this.width - this.width * this.ratio * 2);
    },
    heightWithMargin() {
      return this.height - this.marginTop - this.marginBottom
    },
    getCurrentData () {

      return this.currentData.charAt(0).toUpperCase() + this.currentData.slice(1);
    }
  },
  methods: {
    createSvg() {
      this.svg = d3.select('#'+this.id)
      .append('svg')
      .attr('width', this.width)
      .attr('height', this.height);
    },
    getDataItem(dataInit, item) {
      let self = this;
      let data = {};

      //find current data
      for (let item of dataInit.parsed) {
        if (item.title === item) {
          data.title  = item.title;
          data.values = item.frequencies;
          data.frequencies = dataInit.frequencies.legend_graph.axe_x;
          data.max = dataInit.frequencies.max;
          data.color = item.color;
          break;
        }
      }

      return data;
    },
    createdBarGraph(parent, data) {
        let self = this;

         self.x = d3.scaleBand()
          .range([0,self.barRange])
          .padding(0);
         self.y = d3.scaleLinear()
          .range([self.heightWithMargin, 0]);

        let initialGroup = parent.append('g')
          .attr('transform',
            'translate(' + self.paddingLeft + ', '+ self.marginTop +')');

      //Scale Data
      self.x.domain(data.frequencies.map((d) => d ));
      self.y.domain([0, data.max]);

      self.bar = initialGroup.selectAll('.bar')
          .data(data.frequencies)
          .enter().append('g')
          .attr('class', 'bar bar-'+data.title);

        self.bar.append('line')
          .attr('class', 'line-out')
          .attr('x1', (d) => self.x(d))
          .attr('y1', (d,i) => self.heightWithMargin)
          .attr('x2', (d) => self.x(d))
          .attr('y2', (d, i) => self.heightWithMargin)
          .attr('stroke-width', 1)
          .attr('stroke', '#000');

      self.bar.append('line')
        .attr('class', 'line-in')
        .attr('x1', (d) => self.x(d) + self.x.bandwidth() / 2)
        .attr('y1', (d,i) => self.heightWithMargin )
        .attr('x2', (d) => self.x(d) + self.x.bandwidth() / 2)
        .attr('y2', (d, i) => self.heightWithMargin)
        .attr('stroke-width', 1)
        .attr('stroke', '#000')
//        .transition()
//        .delay(250)
//        .duration(500)
//        .ease(d3.easeCubic)
//        .attr('y2', (d, i) =>self.y(data.values[i]) < self.heightWithMargin ?self.y(data.values[i]) + self.x.bandwidth() * self.ratioLosange / (1 + self.ratioLosange) : self.heightWithMargin);

      self.bar.append('line')
        .attr('class', 'line-out')
        .attr('x1', (d) => self.x(d) + self.x.bandwidth())
        .attr('y1', (d,i) => self.heightWithMargin)
        .attr('x2', (d) => self.x(d) + self.x.bandwidth())
        .attr('y2', (d, i) => self.heightWithMargin)
        .attr('stroke-width', 1)
        .attr('stroke', '#000')

      self.bar.append('rect')
        .attr('width', self.x.bandwidth())
        .attr('height', self.x.bandwidth())
        .attr('transform', (d, i) => 'translate(' + +(self.x(d) + self.x.bandwidth() / 2) + ',' + +(self.y(data.values[i]) - self.x.bandwidth() / 4 + 2) + ') rotate(45)')
        .style('fill', (d, i) =>self.y(data.values[i]) < self.heightWithMargin ? data.color : 'none')
        .style('opacity', 0)


        initialGroup.append('g')
          .attr('transform', 'translate(0,' + self.heightWithMargin + ')')
          .call(d3.axisBottom(self.x));

        // add the y Axis
        parent.append('g')
          .attr('transform', 'translate('+self.marginLeft+',' + self.marginTop + ')')
          .call(d3.axisLeft(self.y));

        parent.append('line')
          .attr('x1', self.marginLeft)
          .attr('y1', self.height - self.marginBottom)
          .attr('x2', self.width)
          .attr('y2', self.height - self.marginBottom)
          .attr('stroke-width', 1)
          .attr('stroke', '#000');
    },
    closeBar(item, data) {
      let self = this;
      let items = d3.selectAll('.bar-'+item+' line')
        .transition()
        .duration(200)
        .ease(d3.easeCubic)
        .attr('y2', (d, i) => self.heightWithMargin);

      d3.selectAll('.bar-'+item+' rect')
        .transition()
        .duration(250)
        .ease(d3.easeLinear)
        .attr('transform', (d, i) => 'translate(' + +(self.x(d) + self.x.bandwidth() / 2) + ',' + +(self.y(data.values[i]) - self.x.bandwidth() / 4 + 2) + ') rotate(45)')
        .style('opacity', 0)
    },
    openBar(item, data) {
      let self = this;
       d3.selectAll('.bar-'+item)
         .each(function (d, i, j) {
           d3.select(this)
             .selectAll('.line-out')
             .transition()
             .duration(350)
             .ease(d3.easeCubic)
             .attr('y2', (d, j) =>self.y(data.values[i]) < self.heightWithMargin ? self.y(data.values[i]) + self.x.bandwidth() * self.ratioLosange / 4 : self.heightWithMargin);
         });

      d3.selectAll('.bar-'+item)
        .each(function (d,i,j) {
          d3.select(this)
            .selectAll('.line-in')
            .transition()
            .duration(350)
            .ease(d3.easeCubic)
            .attr('y2', (d, j) =>self.y(data.values[i]) < self.heightWithMargin ?self.y(data.values[i]) + self.x.bandwidth() * self.ratioLosange / (1 + self.ratioLosange) : self.heightWithMargin);
        });

      d3.selectAll('.bar-'+item+' rect')
        .transition()
        .delay(100)
        .duration(250)
        .ease(d3.easeElastic)
        .attr('transform', (d, i) => 'translate(' + +(self.x(d) + self.x.bandwidth() / 2) + ',' + +(self.y(data.values[i]) - self.x.bandwidth() / 4 + 2) + ') scale(0.75, ' + self.ratioLosange + ')  rotate(45)')
        .duration(250)
        .ease(d3.easeLinear)
        .style('opacity', 1);
    }


  },
  mounted() {
    this.width = Math.round(+window.getComputedStyle(this.$el).width.split('px')[0]);
    this.height = Math.round(+window.getComputedStyle(this.$el).height.split('px')[0]);
    this.createSvg();
    for (let item of this.data.parsed) {
      let data = {};
      data.title = item.title;
      data.values = item.frequencies;
      data.frequencies = this.data.frequencies.legend_graph.axe_x;
      data.max = this.data.frequencies.max;
      data.color = item.color;
      this.createdBarGraph(this.svg, data);
      if(store.currentItems.find((d) => { return d === item.title } )) {
        this.openBar(item.title, data);
      }
    }

    for(let item of store.currentItems) {
    }
  },
    watch: {
      'state.currentItems': function () {
        for (let item of this.data.parsed) {
          let data = {};
          data.title = item.title;
          data.values = item.frequencies;
          data.frequencies = this.data.frequencies.legend_graph.axe_x;
          data.max = this.data.frequencies.max;
          data.color = item.color;
          if (store.currentItems.find((d) => {
              return d === item.title
            })) {
            this.openBar(item.title, data);
          } else {
            this.closeBar(item.title, data)
          }
        }
      }
    }

}
</script>

<style>

</style>



