<template>
  <main class="main-container graph-container">
    <div :id="id"></div>
  </main>
</template>

<script type="text/babel">
  import * as d3 from "d3";
  export default {
    props: {
      data: Object
    },
    data() {
      return {
        width: 650,
        height: 460,
        svg: null,
        rectWidth: 5,
        rectHeight: 5,
        percentOffset: 25,
        id: 'pixelGraph-container'

      }
    },
    methods: {
      test() {
//        console.log('tata');
      },
      createSvg () {
        this.svg = d3.select('#'+this.id)
          .append('svg')
          .attr('width', this.width)
          .attr('height', this.height);
      },

      createPixelsGraph(parent, dataInit) {
        let self = this;
        let grid_cells = this.getGridCells(self.width, self.height, self.rectWidth, self.rectHeight, self.percentOffset);
        let min = dataInit.min;
        let max = dataInit.max;
        let data = dataInit.parsed;
        const scale = d3.scaleLinear()
          .domain([min, max])
          .range([0, 1 - self.percentOffset / 100]);

        //Get positions
        let positions = [];

        for (let i of data) {

          let tmp_positions = [];
          let length = Math.round(scale(i.num) * grid_cells);

          for (let j = 0; j < length; j++) {
            let position;
            let find = false;
            let compare;
            do {
              find = false;
              position = this.randomPosition(self.width, self.height, self.rectWidth, self.rectHeight);
              for (let p of positions) {
                compare = p.find(function (pos) {
                  return (position.x >= pos.x - self.rectWidth && position.x <= pos.x + self.rectWidth
                  && position.y >= pos.y - self.rectHeight && position.y <= pos.y + self.rectHeight);
                });
                if (compare) {
                  find = true;
                  break;
                }
              }
            } while (find);

            tmp_positions.push(position);

          }
          positions.push(tmp_positions)
        }

        let append = parent
          .selectAll('.pixels-container')
          .data(data)
          .enter()
          .append('g')
          .attr('id', d => d.title)
          .attr('class', 'pixels-container')
          .each(function (d, j) {
            let index = j,
              title = d.title,
              color = d.color;
            d3.select(this)
              .selectAll('.pixel')
              .data(d => new Array(Math.round(scale(d.num) * grid_cells)))
              .enter()
              .append('rect')
              .attr('class', 'pixel pixel-'+ title)
//              .attr('x', (d, i) => positions[index][i].x)
//              .attr('y', (d, i) => positions[index][i].y)
              .attr('transform', function (d, i) {
                return 'translate(' + parseInt(positions[index][i].x) + ' ' + parseInt(positions[index][i].y) + ')scale(0)'
              })
              .attr('width', self.rectWidth)
              .attr('height', self.rectHeight)
              .style('fill', 'none')
              .style('opacity', 0)

              .on("mouseenter", function () {
                d3.selectAll('.pixel')
                  .transition()
                  .duration(100)
                  .style('opacity', 0.4);

                d3.selectAll('.pixel-'+title)
                  .transition()
                  .style('opacity', 1)
                  .attr('transform', function (d, i) {
                    return 'translate(' + parseInt(positions[index][i].x - self.rectWidth ) + ' ' + parseInt(positions[index][i].y - self.rectHeight ) + ')scale(2)'
                  })

              })
              .on("mouseleave", function () {
//                console.log(self);
                d3.selectAll('.pixel')
                  .transition()
                  .duration(150)
                  .style('opacity', 1);
                  d3.selectAll('.pixel-' + title)
                    .transition()
                    .attr('transform', function (d, i) {
                      return 'translate(' + parseInt(positions[index][i].x - self.rectWidth / 2) + ' ' + parseInt(positions[index][i].y - self.rectHeight / 2) + ')scale(1)'
                    })
              })
              .transition(t)
              .style('opacity', 1)
              .style('fill', color)
              .attr('transform', function (d, i) {
                return 'translate(' + parseInt(positions[index][i].x - self.rectWidth / 2) + ' ' + parseInt(positions[index][i].y - self.rectHeight / 2) + ')scale(1)'
              })

          });

        let t = d3.transition()
          .delay(250)
          .duration(250)
          .ease(d3.easeLinear);
      },
      getGridCells(width = window.innerWidth, height = window.innerHeight, div_width = 5, div_height = 5, offset = 0) {
        let cells = Math.round(width / div_width) + Math.round(height / div_height);
        return cells - Math.round(cells / offset);
      },
      randomPosition(width = window.innerWidth, height = window.innerHeight, offsetWidth = 0, offsetHeight = 0) {
        let position = {};
        position.x = Math.round(Math.random() * (width - offsetWidth));
        position.y = Math.round(Math.random() * (height - offsetHeight));
        return position;
      },
    },
    mounted() {
      this.width = Math.round(+window.getComputedStyle(this.$el).width.split('px')[0]);
      this.height = Math.round(+window.getComputedStyle(this.$el).height.split('px')[0]);

      this.createSvg();
      this.createPixelsGraph(this.svg, this.data)
      this.$on('hover-country', function () {
//        console.log('hover')
      })
    }
  }


</script>

<style type="text/css">

</style>



